import React from "react"
import { StaticImage } from "gatsby-plugin-image"

export function ProfilePhoto() {
  return (
    <StaticImage src="../images/gmlunesa-pic.jpg" alt="Goldy Mariz Lunesa" />
  )
}

export function AboutPageFirstPhoto() {
  return (
    <StaticImage
      src="../images/ahagon.jpg"
      alt="Ahagon New Game"
      className="about-img-wrapper"
    />
  )
}

export function AboutPageSecondPhoto() {
  return (
    <StaticImage
      src="../images/hal.jpg"
      alt="2001:Space Odyssey still"
      className="about-img-wrapper"
    />
  )
}

export function AboutPageThirdPhoto() {
  return (
    <StaticImage
      src="../images/rei.jpg"
      alt="Rei Ayanami fishing"
      className="about-img-wrapper"
    />
  )
}

export function AboutPageFourthPhoto() {
  return (
    <StaticImage
      src="../images/dooku.jpg"
      alt="Count Dooku"
      className="about-img-wrapper"
    />
  )
}
